@font-face {
    font-family: Circular;
    src: url(https://sp-bootstrap.global.ssl.fastly.net/8.0.0/fonts/circular-medium.woff2) format("woff2"), url(https://sp-bootstrap.global.ssl.fastly.net/8.0.0/fonts/circular-medium.woff) format("woff");
    font-weight: 500;
    font-style:normal
  }
.auth-container{
    text-align: center;
    color: white;
    padding: 3em;
  }
  .auth-container__description{
    margin: 2em 0;
  }  
  .auth-container__button{
      padding: 0.9em;
      font-family: Circular;
      font-size: 0.9em;
      text-decoration: none;
      line-height: 0.9em;
      background-color: #1db954;
      color: white;
      border-radius: 1.8em;
      border: none;
  }

@media screen and (max-width: 500px){

  .auth-container{
  padding: 2em;
    }
    .auth-container__description{
      font-size: 1.4em;
    }
  
}